.main_div h1,
.main_div a {
  color: rgb(255, 206, 39);
}

.main_div a {
  transition: all 0.5s;
}

.main_div a:hover {
  text-decoration: none;
  color: rgb(112, 197, 221);
  font-size: 2.8rem;
}

.contact_img {
  width: 4.5rem;
  transition: all 0.3s;
}

.contact_img:hover {
  width: 5.2rem;
}

